import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min.js";
import { DndProvider } from "react-dnd";
import { HTML5Backend } from "react-dnd-html5-backend";
import DragDropItem from "./DragDropItem";
import DropZone from "./DropZone";

const CORRECT_ANSWERS = {
  "1. Vt cht": [
    "Tn ti khch quan, c lp vi  thc",
    "L ci c trc, quyt nh  thc",
  ],
  "2. Ngun gc, bn cht v kta cu ca  thc": [
    "Ngun gc t nhin: b no ngi",
    "Ngun gc x hi: lao ng v ngn ng",
    "Bn cht: phn nh nng ng, sng to th gii khch quan",
    "Kt cu: tri thc, tnh cm, nim tin,  ch",
  ],
  "3. Mi quan h gia vt cht v  thc": [
    "Vt cht quyt nh  thc",
    " thc tc ng tr li vt cht thng qua thc tin",
    "Mi quan h mang tnh bin chng",
  ],
  "1. Hai loi hnh bin chng": [
    "Bin chng khch quan: vn ng ca t nhin v x hi",
    "Bin chng ch quan: qu trnh t duy v nhn thc",
  ],
  "2. Ni dung v hai nguyn l c bn": [
    "Nguyn l mi lin h ph bin",
    "Mi s vt u lin h v tc ng ln nhau",
    "Nguyn l pht trin",
    "S vt lun vn ng, pht trin qua mu thun ni ti",
    "Quy lut lng  cht",
    "Quy lut mu thun",
    "Quy lut ph nh ca ph nh",
  ],
};

const DRAG_ITEMS = Object.values(CORRECT_ANSWERS).flat();

export default function PosterAnalysisApp() {
  const [zones, setZones] = useState(
    Object.keys(CORRECT_ANSWERS).reduce((acc, key) => {
      acc[key] = [];
      return acc;
    }, {})
  );

  const [availableAnswers, setAvailableAnswers] = useState(DRAG_ITEMS);

  const handleRemove = (zoneKey, answer) => {
    setZones((prev) => ({
      ...prev,
      [zoneKey]: prev[zoneKey].filter((item) => !item.includes(answer)),
    }));
    setAvailableAnswers((prev) => [...prev, answer]);
  };

  const handleDrop = (zoneKey, answer) => {
    const isCorrect = CORRECT_ANSWERS[zoneKey]?.includes(answer);
    const label = isCorrect ? ` ${answer}` : ` ${answer}`;

    setZones((prev) => ({
      ...prev,
      [zoneKey]: [...prev[zoneKey], label],
    }));

    setAvailableAnswers((prev) => prev.filter((item) => item !== answer));
  };

  const [currentPage, setCurrentPage] = useState(0);

  return (
    <div className="min-vh-100 bg-light p-4">
      <div className="container-fluid">
        {/* Nt chuyn trang */}
        <div className="d-flex justify-content-center mb-3">
          <button
            className="btn btn-outline-primary me-2"
            onClick={() => setCurrentPage(0)}
            disabled={currentPage === 0}
          >
             Poster
          </button>
          <button
            className="btn btn-outline-success"
            onClick={() => setCurrentPage(1)}
            disabled={currentPage === 1}
          >
            Mind Map 
          </button>
        </div>

        {/* Vng trt ngang */}
        <div
          className="d-flex transition-all"
          style={{
            width: "200%",
            transform: `translateX(-${currentPage * 50}%)`,
            transition: "transform 0.5s ease",
            gap: "2rem",
          }}
        >
          {/* Slide 1: Poster + Phn tch */}
          <div className="w-50 pe-3 col-md-8">
            <div className="card shadow h-100 ">
              <div className="card-body d-flex">
                <div className="me-5" style={{ flex: "0 0 35%" }}>
                  <h2 className="h5 fw-bold">Poster</h2>
                  <div className="border rounded overflow-hidden text-center">
                    <img
                    alt="Poster"
                    className="img-fluid rounded shadow mt-2"
                    src={process.env.PUBLIC_URL + "/poster.jpg"}
                    style={{
                      maxHeight: "100%",
                      objectFit: "contain",
                      width: "100%",
                    }}
                  />

                  </div>
                </div>

                {/* Phn tch poster */}
                <div
                  style={{
                    flex: "1 1 auto",
                    fontSize: "1.2rem",
                    paddingTop: "1rem",
                  }}
                >
                  <section className="mt-4">
                    <h2 className="h6 fw-bold text-primary mb-3">
                       Phn tch di gc nhn Ch ngha Duy vt Bin chng
                    </h2>
                    {/* Gii thiu hnh nh */}
                    <section className="mb-4">
                      <h3 className="h6 fw-bold text-secondary">
                         Gii thiu hnh nh
                      </h3>
                      <ul className="small mt-2">
                        <li>
                          Tri t, b no, dy xch, bnh rng, nh sng.
                        </li>
                        <li>
                          Th hin mi quan h gia con ngi, cng ngh v nhn
                          thc.
                        </li>
                      </ul>
                    </section>

                    {/* Vt cht v  thc */}
                    <section className="mb-4">
                      <h3 className="h6 fw-bold text-secondary">
                         Vt cht v  thc
                      </h3>
                      <ul className="small mt-2">
                        <li>
                          <strong>Tri t</strong>: biu tng ca vt cht
                          khch quan.
                        </li>
                        <li>
                          <strong>B no</strong>: biu tng ca  thc  sn
                          phm ca vt cht c t chc cao.
                        </li>
                        <li>
                          <strong>Dy xch</strong>: cho thy  thc b chi phi
                          bi iu kin vt cht.
                        </li>
                        <li>
                          <strong>Cu thang ln nh sng</strong>: qu trnh
                          nhn thc v pht trin t duy.
                        </li>
                      </ul>
                    </section>

                    {/* Php bin chng duy vt */}
                    <section className="mb-4">
                      <h3 className="h6 fw-bold text-secondary">
                         Php bin chng duy vt
                      </h3>

                      <p className="small mb-1">
                        <strong>Hai loi hnh bin chng:</strong>
                      </p>
                      <ul className="small">
                        <li>
                          <strong>Khch quan</strong>: Tri t, bnh rng, mch
                          in  s vn ng ca th gii vt cht.
                        </li>
                        <li>
                          <strong>Ch quan</strong>: B no, nh sng  qu
                          trnh t duy v nhn thc ca con ngi.
                        </li>
                      </ul>

                      <p className="small mt-3 mb-1">
                        <strong>Hai nguyn l c bn:</strong>
                      </p>
                      <ul className="small">
                        <li>
                          <strong>Mi lin h ph bin</strong>: dy xch kt
                          ni mi yu t  mi s vt u lin h v tc ng
                          ln nhau.
                        </li>
                        <li>
                          <strong>S pht trin</strong>: cu thang ln nh sng
                           vn ng t thp n cao, t cha bit n bit.
                        </li>
                      </ul>
                    </section>

                    {/* Thng ip tng th */}
                    <section>
                      <h3 className="h6 fw-bold text-secondary">
                         Thng ip tng th
                      </h3>
                      <ul className="small mt-2">
                        <li>
                          Vt cht l nn tng,  thc l cng c ci bin th
                          gii.
                        </li>
                        <li>
                          Con ngi ang trn hnh trnh nhn thc, vt qua
                          rng buc  hng ti tri thc v t do.
                        </li>
                        <li>
                          Cng ngh, tr tu v s vn ng khng ngng l cha
                          kha  hiu v thay i th gii.
                        </li>
                      </ul>
                    </section>
                  </section>
                </div>
              </div>
            </div>
          </div>

          {/* Slide 2: Mind Map ko th */}
          <div className="w-50 ps-3" style={{ paddingRight: '2rem' }}>
            <div className="card shadow h-100">
              <div className="card-body">
                <button
                  className="btn btn-outline-danger btn-sm mb-3"
                  onClick={() => {
                    setZones(
                      Object.keys(CORRECT_ANSWERS).reduce((acc, key) => {
                        acc[key] = [];
                        return acc;
                      }, {})
                    );
                    setAvailableAnswers([...DRAG_ITEMS]);
                  }}
                >
                   Lm li
                </button>

                <DndProvider backend={HTML5Backend}>
                  <h2 className="h5 fw-bold mb-4 text-primary">
                     Ko th p n ng vo tng vng kin thc
                  </h2>

                  <div className="mb-4">
                    <h6 className="fw-semibold"> p n c th ko:</h6>
                    <div className="d-flex flex-wrap">
                      {availableAnswers.map((text) => (
                        <DragDropItem key={text} text={text} />
                      ))}
                    </div>
                  </div>

                  {Object.keys(CORRECT_ANSWERS).map((zoneKey) => (
                    <DropZone
                      key={zoneKey}
                      title={zoneKey}
                      acceptedAnswers={zones[zoneKey] || []}
                      onDrop={(text) => handleDrop(zoneKey, text)}
                      onRemove={(text) => handleRemove(zoneKey, text)}
                    />
                  ))}
                </DndProvider>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
